<template>
    <div class="tab-control">
        <div class='tab-control-item' v-for="(item,index) in tabControlItem" :key="index" :class="{active:currentIndex == index}" @click="itemClick(index)">
            <span>{{item}}</span>
        </div>
    </div>
</template>
<script>
export default {
    name:'TabControl',
    data() {
        return {
            currentIndex:0
        }
    },

    props:{
        tabControlItem:{
            type:Array,
            default:[]
        }
    },
    methods: {
        itemClick(index) {
            this.currentIndex = index;
            this.$emit('tabClick',index)
        }
    }
}
</script>
<style scoped>
.tab-control {
    display: flex;
    text-align: center;
      line-height: 40px;
    font-size: 15px;
    background-color: #fff;
}
.tab-control-item {
    flex: 1;
}
span {
    padding: 5px;
}
.active span {
    border-bottom: 2px solid var(--color-high-text);
    color: var(--color-high-text);
}
</style>