<template>
  <div class="home">
    <nav-bar class="home-nav">
      <div slot="center">购物街</div>
    </nav-bar>
    <tab-control
      :tabControlItem="tabControlItem"
      @tabClick="tabClick"
      ref="tabControl1"
      v-show="showTabControl"
      class="tab-control"
    ></tab-control>
    <scroll
      class="wrapper"
      ref="scroll"
      @scroll="contentScroll"
      :probeType="2"
      :pullUpLoad="true"
      @pullingUp="loadMore"
    >
      <home-swiper :banners="banners" @imageLoaded="imageLoaded"></home-swiper>
      <recommend-view :recommends="recommends"></recommend-view>
      <feature-view></feature-view>
      <tab-control
        :tabControlItem="tabControlItem"
        @tabClick="tabClick"
        ref="tabControl2"
        v-show="!showTabControl"
      ></tab-control>
      <goods-list :goods="goods[currentType].list"></goods-list>
    </scroll>
    <back-top @click.native="backClick" v-show="showBackTop"></back-top>
  </div>
</template>
<script>
import NavBar from "components/common/navbar/NavBar";
import { getHomeMultidata, getProductData } from "network/home";
import HomeSwiper from "./childComps/HomeSwiper";
import RecommendView from "./childComps/RecommendView";
import FeatureView from "./childComps/FeatureView";
import TabControl from "components/content/tabControl/TabControl.vue";
import GoodsList from "components/content/goods/GoodsList";
import Scroll from "components/common/scroll/Scroll";
import BackTop from "components/content/backTop/BackTop";
import { debounce } from "common/utils";
import {itemListenerMinxin,backTopMixin} from 'common/mixin.js'
export default {
  name: "Home",
  components: {
    NavBar,
    HomeSwiper,
    RecommendView,
    FeatureView,
    TabControl,
    GoodsList,
    Scroll,
    BackTop,
  },
  data() {
    return {
      banners: [],
      recommends: [],
      tabControlItem: ["流行", "新款", "精选"],
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      currentType: "pop",
  
      tabOffsetTop: 0,
      showTabControl: false,
      saveY: 0
     
    };
  },
  created() {
    this.getHomeMultidata();
    this.getProductData("pop");
    this.getProductData("new");
    this.getProductData("sell");
  },
  activated() {
      this.$refs.scroll.scroll.scrollTo(0,this.saveY,0)
      this.$refs.scroll.refresh();
  },
  deactivated() {
    this.saveY = this.$refs.scroll.scroll.y;
    this.$bus.$off('itemImageLoad',this.itemImageListener)

  },
  mixins:[itemListenerMinxin,backTopMixin],
 
  computed: {},
  methods: {
    imageLoaded() {
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop;
      console.log(this.tabOffsetTop);
    },

    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
      this.$refs.tabControl1.currentIndex = index;
      this.$refs.tabControl2.currentIndex = index;
    },
    contentScroll(position) {
      //回到顶部
      if (-position.y > 1000) {
        this.showBackTop = true;
      }
      if (-position.y > this.tabOffsetTop) {
        this.showTabControl = true;
      }
      if (-position.y <= this.tabOffsetTop) {
        this.showTabControl = false;
      }
    },
 
    loadMore() {
      console.log("home");
      this.getProductData(this.currentType);
    },

    getHomeMultidata() {
      getHomeMultidata().then((res) => {
        this.banners = res.data.banner.list;
        this.recommends = res.data.recommend.list;
      });
    },

    getProductData(type) {
      const page = this.goods[type].page + 1;
      getProductData(type, page).then((res) => {
        const newList = res.data.list;
        this.goods[type].list.push(...newList);
        this.goods[type].page++;
      });
    },
  },
};
</script>
<style scoped>
.home {
  position: relative;
  height: 100vh;
}
.home-nav {
  background-color: var(--color-tint);
  color: white;
  z-index: 9;
}

.wrapper {
  position: absolute;
  top: 44px;
  left: 0;
  right: 0;
  bottom: 49px;
  overflow: hidden;
}

.tab-control {
  position: relative;
  z-index: 9;
}
</style>

